import Button from "../components/UI/Button";
import Header from "../components/Header";
import { useEffect, useState } from "react";
import { Link } from "react-scroll";
import Preview from "../components/Preview";

const Main = () => {
  const [currentImg, setCurrentImg] = useState(1);

  function nextSlide() {
    setCurrentImg((prev) => (prev === 6 ? 1 : ++prev));
  }

  useEffect(() => {
    setInterval(nextSlide, 5000);
  }, []);

  return (
    <section id="main">
      <Header />
      <div className="wrapper info-container">
        <div className="info-block">
          <h1>Изготовление доборных элементов для кровли и фасадов</h1>
          <p>Закажи сейчас, доставим завтра!</p>
          <Link to="catalog" spy={true} smooth={true} duration={1500}>
            <Button>Каталог</Button>
          </Link>
        </div>
      </div>
      <div className="slider">
        {[...Array(6)].map((_, index) => (
          <Preview key={index} currentImg={currentImg} index={index} />
        ))}
      </div>
    </section>
  );
};

export default Main;
