import { useProgressiveImage } from "../hooks/useProgressiveImage";

const Preview = ({ currentImg, index }) => {
  const placeholder = `./assets/images/preview/${index + 1}_placeholder.jpg`;
  const loaded = useProgressiveImage(`./assets/images/preview/${index + 1}.jpg`);

  return (
    <div
      className={`bg ${index + 1 === currentImg ? "active" : ""}`}
      style={{
        filter: `${!loaded ? "blur(5px)" : "none"}`,
        backgroundImage: `url("${loaded || placeholder}")`,
      }}></div>
  );
};

export default Preview;
