import { MdLocalPhone, MdLocationOn } from "react-icons/md";

const Header = () => {
  return (
    <header id="header">
      <div className="wrapper">
        <img src="./assets/images/logo_2.png" alt="Просто жесть" className="logo" />
        <address>
          <div className="phone">
            <span>8 (800) 77-00-41</span>
            <MdLocalPhone />
          </div>
          <div className="street">
            <span>
              г. Ростов-на-дону, <br />
              ул. Обсерваторная, д.42
            </span>
            <MdLocationOn />
          </div>
        </address>
      </div>
    </header>
  );
};

export default Header;
